import React, { useCallback, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  TextField,
} from "@mui/material";
import { Stack } from "@mui/system";
import { Controller, useForm } from "react-hook-form";
import { useRecoilState } from "recoil";
import { timeTableState } from "../store/store";
import { v4 as uuidv1 } from "uuid";
import { getRandomColor } from "../utils/colors";

//  MUI TimePicker  import
import dayjs from "dayjs";
import "dayjs/locale/ko";
import { LocalizationProvider, TimePicker } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";

//      
const parseTimeToDecimal = (timeStr) => {
  if (!timeStr) return null;
  const [h, m] = timeStr.split(":").map(Number);
  return h + m / 60;
};

const checkOverLap = (A, B) => {
  const aStart = parseTimeToDecimal(A.startTime);
  const aEnd = parseTimeToDecimal(A.endTime);
  const bStart = parseTimeToDecimal(B.startTime);
  const bEnd = parseTimeToDecimal(B.endTime);

  if (aStart === null || aEnd === null || bStart === null || bEnd === null) return false;
  return !(aEnd <= bStart || aStart >= bEnd);
};

function InputModal({
  showModal,
  handleClose,
  dayData = "mon",
  startTimeData = "09:00", //    9
  endTimeData = "10:00",   //    10
  lectureNameData = "",
  colorData = "#FFFFFF",
  idNum,
}) {
  const { control, getValues, handleSubmit, reset, formState: { errors } } = useForm({
    defaultValues: {
      lectureName: "",
      day: "mon",
      startTime: "09:00",
      endTime: "10:00",
      lectureColor: "#FFFFFF",
    },
  });

  const [timeTableData, setTimeTableData] = useRecoilState(timeTableState);

  //      
  useEffect(() => {
    if (showModal) {
      reset({
        lectureName: lectureNameData || "",
        day: dayData || "mon",
        startTime: startTimeData || "09:00",
        endTime: endTimeData || "10:00",
        lectureColor: colorData || "#FFFFFF",
      });
    }
  }, [showModal, reset, lectureNameData, dayData, startTimeData, endTimeData, colorData]);

  //   
  const Submit = useCallback(
    ({ lectureName, day, startTime, endTime, lectureColor }) => {
      const valid = !timeTableData[day].some((lec) =>
        checkOverLap(lec, { startTime, endTime })
      );
      if (!valid) {
        alert("    .");
        return;
      }
      const data = {
        startTime,
        endTime,
        name: lectureName,
        color: getRandomColor([]),
        id: uuidv1(),
      };
      setTimeTableData((prev) => ({
        ...prev,
        [day]: [...prev[day], data],
      }));
      handleClose();
    },
    [handleClose, setTimeTableData, timeTableData]
  );

  //  
  const Edit = useCallback(
    ({ lectureName, day, startTime, endTime }) => {
      const valid = !timeTableData[day].some(
        (lec) => checkOverLap(lec, { startTime, endTime }) && lec.id !== idNum
      );
      if (!valid) {
        alert("    .");
        return;
      }
      const filteredDayData = timeTableData[dayData].filter((data) => data.id !== idNum);
      const newDayData = [
        ...filteredDayData,
        { startTime, endTime, id: idNum, name: lectureName, color: getRandomColor([]) },
      ];
      setTimeTableData({
        ...timeTableData,
        [day]: newDayData,
      });
      handleClose();
    },
    [dayData, handleClose, idNum, setTimeTableData, timeTableData]
  );

  const handleDelete = useCallback(() => {
    setTimeTableData((prev) => ({
      ...prev,
      [dayData]: prev[dayData].filter((lec) => lec.id !== idNum),
    }));
    handleClose();
  }, [dayData, idNum, setTimeTableData, handleClose]);

  return (
    <Dialog open={showModal} onClose={handleClose}>
      <form onSubmit={handleSubmit(idNum ? Edit : Submit)}>
        <DialogTitle align="center">  </DialogTitle>
        <DialogContent style={{ width: "400px" }}>
          {/*  */}
          <Controller
            control={control}
            name="lectureName"
            rules={{ required: true }}
            render={({ field }) => (
              <TextField
                {...field}
                error={!!errors.lectureName}
                style={{ marginTop: "30px", width: "350px" }}
                autoComplete="off"
                label=""
              />
            )}
          />

          {/*  */}
          <FormControl style={{ marginTop: "30px" }}>
            <FormLabel></FormLabel>
            <Controller
              control={control}
              name="day"
              rules={{ required: true }}
              render={({ field }) => (
                <RadioGroup {...field} style={{ display: "block" }}>
                  <FormControlLabel value="mon" control={<Radio />} label="" />
                  <FormControlLabel value="tue" control={<Radio />} label="" />
                  <FormControlLabel value="wed" control={<Radio />} label="" />
                  <FormControlLabel value="thu" control={<Radio />} label="" />
                  <FormControlLabel value="fri" control={<Radio />} label="" />
                </RadioGroup>
              )}
            />
          </FormControl>

          {/*  /  - TimePicker (5 ) */}
          <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ko">
            <Stack spacing={3} style={{ marginTop: "30px", width: "350px" }}>
              <Controller
                control={control}
                name="startTime"
                rules={{ required: true }}
                render={({ field }) => (
                  <TimePicker
                    label=" "
                    ampm
                    minutesStep={5}
                    value={dayjs(`1970-01-01T${field.value}`)}
                    onChange={(v) =>
                      field.onChange(v ? dayjs(v).format("HH:mm") : "")
                    }
                    slotProps={{
                      textField: { fullWidth: true },
                    }}
                    PopperProps={{
                      placement: "bottom-start",
                      disablePortal: true,
                    }}
                    desktopModeMediaQuery="all" 
                  />
                )}
              />
              <Controller
                control={control}
                name="endTime"
                rules={{
                  required: true,
                  validate: (v) => v > getValues("startTime"),
                }}
                render={({ field }) => (
                  <TimePicker
                    label=" "
                    ampm
                    minutesStep={5}
                    value={dayjs(`1970-01-01T${field.value}`)}
                    onChange={(v) =>
                      field.onChange(v ? dayjs(v).format("HH:mm") : "")
                    }
                  slotProps={{
                    textField: { fullWidth: true },
                  }}
                  PopperProps={{
                    placement: "bottom-start",
                    disablePortal: true,
                  }}
                  desktopModeMediaQuery="all" 
                  />
                )}
              />
            </Stack>
          </LocalizationProvider>
        </DialogContent>

        <DialogActions>
          {idNum && (
            <Button color="error" onClick={handleDelete}>
              
            </Button>
          )}
          <Button onClick={handleClose}></Button>
          <Button type="submit"></Button>
        </DialogActions>
      </form>
    </Dialog>
  );
}

export default InputModal;
