import React from "react";
import { Box, Typography } from "@mui/material";

const days = ["mon", "tue", "wed", "thu", "fri"];
const times = [
  "09:00", "10:00", "11:00", "12:00", "13:00",
  "14:00", "15:00", "16:00", "17:00", "18:00"
];

function TimeTablePreview({ data }) {
  return (
    <Box
      sx={{
        display: "grid",
        gridTemplateColumns: "50px repeat(5, 1fr)",
        gridTemplateRows: "40px repeat(10, 50px)",
        width: "90%",          
        maxWidth: "600px",      
        margin: "0 auto",
        border: "1px solid #ddd",
        borderRadius: 2,
        overflow: "hidden",
      }}
    >
        
      {/*   */}
      <Box sx={{ gridRow: 1, gridColumn: 1, backgroundColor: "#f9f9f9" }} />
      {days.map((day, i) => (
        <Box
          key={day}
          sx={{
            gridRow: 1,
            gridColumn: i + 2,
            textAlign: "center",
            backgroundColor: "#f9f9f9",
            borderLeft: "1px solid #ddd",
            borderBottom: "1px solid #ddd",
            fontWeight: 600,
          }}
        >
          {["", "", "", "", ""][i]}
        </Box>
      ))}

      {/*   */}
      {times.map((time, i) => (
        <Box
          key={time}
          sx={{
            gridRow: i + 2,
            gridColumn: 1,
            borderTop: "1px solid #eee",
            textAlign: "center",
            fontSize: "0.85rem",
            backgroundColor: "#fafafa",
          }}
        >
          {time}
        </Box>
      ))}

      {/*     */}
      {times.map((_, rowIdx) =>
        days.map((_, colIdx) => (
          <Box
            key={`${rowIdx}-${colIdx}`}
            sx={{
              gridRow: rowIdx + 2,
              gridColumn: colIdx + 2,
              borderTop: "1px solid #f0f0f0",
              borderLeft: "1px solid #f0f0f0",
              backgroundColor: "#fff",
            }}
          />
        ))
      )}

      {/*   */}
      {days.map((day, colIdx) =>
        data[day]?.map((lec, i) => {
          const startHour = parseInt(lec.startTime.split(":")[0]);
          const endHour = parseInt(lec.endTime.split(":")[0]);
          const rowStart = startHour - 8 + 1; // 9   offset
          const rowEnd = endHour - 8 + 2;

          return (
            <Box
              key={`${day}-${i}`}
              sx={{
                gridRowStart: rowStart + 1,
                gridRowEnd: rowEnd + 1,
                gridColumnStart: colIdx + 2,
                gridColumnEnd: colIdx + 3,
                backgroundColor: "#f7e6ef",
                border: "1px solid #ddd",
                borderRadius: 1,
                p: 1,
                textAlign: "center",
                zIndex: 2,
              }}
            >
              <Typography variant="body2" fontWeight={600}>
                {lec.name}
              </Typography>
              <Typography variant="caption">
                {lec.startTime}~{lec.endTime}
              </Typography>
            </Box>
          );
        })
      )}
    </Box>
  );
}

export default TimeTablePreview;
