import { getRandomColor } from "./colors";

function parseTimeToDecimal(timeStr) {
  const [h, m] = timeStr.split(":").map(Number);
  return h + m / 60;
}

function isOverlap(a, b) {
  if (a.day !== b.day) return false;
  const aStart = parseTimeToDecimal(a.startTime);
  const aEnd = parseTimeToDecimal(a.endTime);
  const bStart = parseTimeToDecimal(b.startTime);
  const bEnd = parseTimeToDecimal(b.endTime);
  return !(aEnd <= bStart || aStart >= bEnd);
}

function emptyTable() {
  return { mon: [], tue: [], wed: [], thu: [], fri: [] };
}

function sortByStartTime(dayLectures) {
  return dayLectures.sort(
    (a, b) => parseTimeToDecimal(a.startTime) - parseTimeToDecimal(b.startTime)
  );
}

export function generateAllValidTimetables(selectedLectures) {
  if (!selectedLectures || selectedLectures.length === 0) return [];

  const results = [];
  const seenKeys = new Set();

  //         
  for (const anchor of selectedLectures) {
    const timetable = emptyTable();
    timetable[anchor.day].push({ ...anchor, color: getRandomColor() });

    const usedSubjects = new Set([anchor.name]);

    //         
    const others = selectedLectures
      .filter((l) => l !== anchor)
      .sort((a, b) => parseTimeToDecimal(a.endTime) - parseTimeToDecimal(b.endTime));

    for (const lec of others) {
      //    skip
      if (usedSubjects.has(lec.name)) continue;

      //   
      const hasConflict = Object.values(timetable).some((dayLectures) =>
        dayLectures.some((exist) => isOverlap(exist, lec))
      );
      if (hasConflict) continue;

      timetable[lec.day].push({ ...lec, color: getRandomColor() });
      usedSubjects.add(lec.name);
    }

    //  
    Object.keys(timetable).forEach((day) => {
      timetable[day] = sortByStartTime(timetable[day]);
    });

    //    key
    const key = Object.values(timetable)
      .flat()
      .map((l) => `${l.day}-${l.name}-${l.startTime}-${l.endTime}`)
      .sort()
      .join("|");

    if (!seenKeys.has(key)) {
      seenKeys.add(key);
      results.push(timetable);
    }
  }

  //      (   )
  results.sort((a, b) => {
    const countA = Object.values(a).reduce((s, d) => s + d.length, 0);
    const countB = Object.values(b).reduce((s, d) => s + d.length, 0);
    return countB - countA;
  });

  return results;
}
